import { UI_ELEMENTS } from './view.js'
import {
    parseISO,
    isFuture,
    differenceInHours,
    differenceInDays,
    differenceInYears,
    differenceInMinutes,
    differenceInSeconds
} from 'date-fns'

let timer = null
const DATE_ERROR_TEXT = 'Enter a future date!'
const state = {
    ERRORS: [DATE_ERROR_TEXT, 'UUUPS, this day is now!'],
}

UI_ELEMENTS.DATE_FORM.addEventListener('input', (e) => {
    e.preventDefault()
    timer = setTimeout(function tick() {
        renderDate()
        timer = setTimeout(tick, 1000)
    })
})

UI_ELEMENTS.DATE_BUTTON.addEventListener('click', () => {
    clearTimeout(timer)
    UI_ELEMENTS.DATE_INPUT.classList.remove('error')
    UI_ELEMENTS.DATE_ANSWER.textContent = ''
    UI_ELEMENTS.DATE_FORM.reset()
})

function renderDate() {
    const currentDate = parseISO(UI_ELEMENTS.DATE_INPUT.value)
    let getSeconds = differenceInSeconds(currentDate, new Date())
    let getMinutes = differenceInMinutes(currentDate, new Date())
    let getHours = differenceInHours(currentDate, new Date())
    let getDays = differenceInDays(currentDate, new Date())
    let getYears = differenceInYears(currentDate, new Date())

    getSeconds = getSeconds - (getMinutes * 60)
    getMinutes = getMinutes - (getHours * 60)
    getHours = getHours - (getDays * 24)
    getDays = getDays - (getYears * 365)

    if (state.ERRORS[0]) {
        UI_ELEMENTS.DATE_INPUT.classList.remove('error')
        clearTimeout(timer)
    }

    if (isFuture(currentDate)) {
        UI_ELEMENTS.DATE_ANSWER.textContent = `${getTime(getYears, ' year(s)')} 
        ${getTime(getDays, ' day(s)')} 
        ${getTime(getHours, ' hour(s)')} 
        ${getTime(getMinutes, ' minute(s)')} 
        ${getTime(getSeconds, ' second(s)')} 
        left until the future date`
    } else {
        UI_ELEMENTS.DATE_INPUT.classList.add('error')
        return UI_ELEMENTS.DATE_ANSWER.textContent = state.ERRORS[0]
    }
}

function getTime(time, word) {
    return time ? time + word : ''
}

